import React from 'react';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import Typography from "@material-ui/core/Typography";

function Home() {
  const posts = [
    {date: "March 17, 2020", title: "Cool New Status Updates", text: <div><p>Adding status updates section for funsies. There's not enough interesting stuff to put here so we're really reaching for content.</p></div>},
    {date: "March 11, 2020", title: "Website is Online", text: <div><p>The website is now online. We're expanding various sections here and there to add some variety.</p></div>}
  ];
  return (
      <div>
        {posts.map(post => {
          return (
            <Card variant="outlined" style={{marginTop: "10px", marginBottom: "10px"}}>
              <CardContent>
              <Typography variant="h4" component="h4">{post.title}</Typography>
                <Typography variant="subtitle1">{post.date}</Typography>
                <Typography variant="body1">{post.text}</Typography>
              </CardContent>
            </Card>
          );
        })}
      </div>
  );
}

export default Home;
