import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import Card from "@material-ui/core/Card";
import CardActionArea from "@material-ui/core/CardActionArea";
// import CardActions from '@material-ui/core/CardActions';
import CardContent from "@material-ui/core/CardContent";
import CardMedia from "@material-ui/core/CardMedia";
// import Button from '@material-ui/core/Button';
import Typography from "@material-ui/core/Typography";
import { useHistory } from "react-router-dom";

import { Star, Movie } from "@material-ui/icons";

// import Paper from "@material-ui/core/Paper";
// import Grid from "@material-ui/core/Grid";

const useStyles = makeStyles(theme => ({
  root: {
    flexGrow: 1
  },
  root2: {
    maxWidth: 345
  },
  paper: {
    padding: theme.spacing(2),
    textAlign: "center",
    color: theme.palette.text.secondary
  }
}));

const reviews = [
  { title: "The Incredibles (2004)", alt: "theincredibles2004", path: "/reviews/film/theincredibles", imgurl: "https://i.imgur.com/P7dBcZv.jpg", rating: <div><Star /><Star /><Star /><Star /><Star /></div>, type: "film", icon: <Movie />, quote: "This film exemplifies masterful storytelling through compelling character arcs, lifelike dialogue, and evocative conflict. Easily my favorite film. " },
];

export default function FilmReviews() {
  const classes = useStyles();
  let history = useHistory();

  return (
    <div className={classes.root}>
        {reviews.map(review => {
          return (
              <Card
                className={classes.root2}
                style={{marginTop: "10px", marginBottom: "10px"}}
                onClick={() => {
                  history.push(review.path);
                }}
              >
                <CardActionArea>
                  <CardMedia component="img" alt={review.alt} height="480" image={review.imgurl} title={review.title} />
                  <CardContent>
                    <Typography gutterBottom variant="h5" component="h2">
                      <div style={{display: "flex", justifyContent: "space-between"}}>
                      {review.title} {review.icon}
                      </div>
                    </Typography>
                    <Typography variant="body2" color="textSecondary" component="p">
                      "{review.quote}"
                    </Typography>
                    <Typography variant="body2" color="textSecondary" component="p">
                      {review.rating}
                    </Typography>
                  </CardContent>
                </CardActionArea>
              </Card>
          );
        })}
    </div>
  );
}